.request__call {
  display: none;
  position: absolute;
  width: 320px;
  height: 676px;
  background-color: #ffffff;
  z-index: 2;
  cursor: pointer; }
  .request__call--close {
    position: absolute;
    margin-top: 24px;
    margin-left: 16px; }
  .request__call--highlight {
    position: absolute;
    margin-top: 96px; }
  .request__call--text {
    font-family: 'TTLakes-Bold';
    position: absolute;
    width: 288px;
    height: 32px;
    transform: translate(16px, 104px);
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    color: #1b1c21; }
  .request__call--phone {
    position: absolute;
    transform: translate(16px, 160px); }
    .request__call--phone .enter__phone {
      width: 288px;
      height: 48px;
      background: #ffffff;
      border: 1px solid #eaeaea;
      border-radius: 8px;
      font-size: 16px; }
    .request__call--phone .enter__phone::-webkit-input-placeholder {
      font-family: 'TTLakes-Medium';
      transform: translateX(16px);
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #b5b6bc; }
    .request__call--phone .enter__phone::-moz-placeholder {
      font-family: 'TTLakes-Medium';
      transform: translateX(16px);
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #b5b6bc; }
    .request__call--phone .enter__phone:-ms-input-placeholder {
      font-family: 'TTLakes-Medium';
      transform: translateX(16px);
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #b5b6bc; }
    .request__call--phone .enter__phone::placeholder {
      font-family: 'TTLakes-Medium';
      transform: translateX(16px);
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #b5b6bc; }
  .request__call--processing {
    position: absolute;
    width: 288px;
    height: 64px;
    transform: translate(16px, 224px);
    font-family: 'TTLakes-Medium';
    font-size: 14px;
    line-height: 16px;
    color: #7e7e82; }
    .request__call--processing .processing__text {
      color: #ff3e79; }
  .request__call--send {
    transform: translate(168px, 306px); }
