    border: 1px solid #eaeaea;
    transition: border 0.5s ease-out;
    cursor: pointer; }
    .swiper.one .swiper-slide .lenovo__img {
      position: absolute;
      margin: 20px 16px; }
    .swiper.one .swiper-slide .icon__go {
      margin-top: 16px;
      margin-left: 184px; }
    .swiper.one .swiper-slide .samsung__img {
      position: absolute;
      margin: 17px 16px; }
    .swiper.one .swiper-slide .apple__img {
      position: absolute;
      margin: 12px 16px; }
    .swiper.one .swiper-slide .viewsonic__img {
      position: absolute;
      margin: 26px 14px; }
    .swiper.one .swiper-slide .bosch__img {
      position: absolute;
      margin: 21.7px 16px; }
    .swiper.one .swiper-slide .hp__img {
      position: absolute;
      margin: 10px 10px; }
    .swiper.one .swiper-slide .acer__img {
      position: absolute;
      margin: 24px 16px; }
    .swiper.one .swiper-slide .sony__svg {
      position: absolute;
      margin: 23px 14px; }
  .swiper.one .swiper-slide:hover {
    border: 1px solid #b8ffec;
    border-radius: 6px;
    transition: border 0.5s ease-out;
    z-index: 2; }
