@media (min-width: 1366px) {
  .request__call {
    width: 440px; }
    .request__call--close {
      transform: translate(360px, -10px); }
    .request__call--highlight {
      position: absolute;
      margin-top: 32px; }
    .request__call--text {
      font-family: 'TTLakes-Bold';
      position: absolute;
      width: 190px;
      height: 32px;
      transform: translate(32px, 40px); }
    .request__call--phone {
      transform: translate(32px, 96px); }
      .request__call--phone .enter__phone {
        width: 376px; }
    .request__call--processing {
      font-family: 'TTLakes-Regular';
      width: 376px;
      height: 48;
      transform: translate(32px, 160px); }
      .request__call--processing br {
        display: none; }
    .request__call--send {
      transform: translate(272px, 224px); } }
