header .header__icons {
  display: flex;
  width: 320px;
  height: 88px;
  margin: 0 auto; }
  header .header__icons .icon__burger {
    width: 40px;
    height: 40px;
    margin-top: 24px;
    margin-left: 16px; }
  header .header__icons .icon__divider--768 {
    display: none;
    width: 2px;
    height: 32px; }
  header .header__icons .icon__group {
    width: 96.55px;
    height: 50.81px;
    margin-top: 18.59px;
    margin-left: 16.73px; }
  header .header__icons .icon__divider {
    width: 2px;
    height: 32px;
    background-color: #eaeaea;
    border-radius: 1px;
    margin-top: 28px;
    margin-left: 16.73px; }
  header .header__icons .icon__repair {
    width: 40px;
    height: 40px;
    margin-top: 24px;
    margin-left: 20px; }
  header .header__icons .icon__checkst {
    width: 40px;
    height: 40px;
    margin-top: 24px;
    margin-left: 16px; }

header .icon__shadow {
  display: block;
  margin: 0 auto; }

.icon__call {
  display: none; }

.icon__chat {
  display: none; }

.icon__profile {
  display: none; }
